import classes from "./recordings.module.scss";
import ReactPlayer from "react-player";
import { Parallax } from "react-parallax";

function Recordings() {
  const Container = () => (
    <Parallax
      blur={1}
      bgImage="/pictures/gallery7.JPG"
      bgImageAlt="contact picture"
      strength={300}
      bgClassName={classes.background}
    >
      <div className={classes.content}>
        <div className={classes.gradiant} />
        <div className={classes.mainHeader}>Recordings</div>
      </div>
    </Parallax>
  );
  return (
    <div className={classes.recordings}>
      <Container />
      <div className={classes.textWrapper}>
        <ReactPlayer
          url="https://youtu.be/pmUs88HvuhM"
          width={450}
          height={450}
        />
        <ReactPlayer
          url="https://youtu.be/4q-GHriLCT8"
          width={450}
          height={450}
        />
      </div>
    </div>
  );
}

export default Recordings;
