import classes from "./ContactItem.module.scss";

function ContactItem({ iconPaths, id, contactDetails }) {
  return (
    <div className={classes.contactItem}>
      <div
        className={classes.icon}
        style={{
          maskImage: iconPaths[id],
          WebkitMaskImage: iconPaths[id],
        }}
      ></div>
      <div className={classes.text}>{contactDetails[id]}</div>
    </div>
  );
}

export default ContactItem;
