import classes from "./Contact.module.scss";
import { Parallax } from "react-parallax";
import ContactItem from "./ContactItem";

const iconPaths = [
  "url('/icons/instagramIcon.png')",
  "url('/icons/mailIcon.png')",
  "url('/icons/phoneIcon.png')",
];

const contactDetails = ["instegram", "leikieglick@gmail.com", "+1(267)5883489"];

function Contact() {
  const Container = () => (
    <Parallax
      blur={0}
      bgImage="/pictures/contact.JPG"
      bgImageAlt="contact picture"
      strength={300}
      bgClassName={classes.background}
    >
      <div className={classes.content}>
        <div className={classes.gradiant} />
        <div className={classes.mainHeader}>Contact</div>
      </div>
    </Parallax>
  );
  return (
    <div className={classes.contact}>
      <Container />
      <div className={classes.textWrapper}>
        <ContactItem
          iconPaths={iconPaths}
          contactDetails={contactDetails}
          id={0}
        />
        <ContactItem
          iconPaths={iconPaths}
          contactDetails={contactDetails}
          id={1}
        />
        <ContactItem
          iconPaths={iconPaths}
          contactDetails={contactDetails}
          id={2}
        />
      </div>
    </div>
  );
}

export default Contact;
